.expirtes {
    background: linear-gradient(90deg, rgba(0, 182, 158, 0.386) 0%, rgb(76 244 244 / 0%) 50%, rgba(165, 243, 243, 0.53) 100%);
}
.expirtes-info {
    padding: 4rem 3rem;
    display: flex;
    flex-direction: column-reverse;
    justify-content:center;
    align-items: center;
}
.expirtes-info .info {
    margin: 4rem;
    max-width: 500px;
}
.name {
    padding: 1rem;
    font-size: 70px;
    font-family: var(--font-base);
    color: rgba(16, 116, 163, 0.77);
}
.self-image img {
    height:600px ;
    border-radius: 15%;
}
.expirtes p {
    padding-top: 2rem;
    font-size: 25px;
    font-weight: 100;
    font-family: var(--font-sec);
    color: rgb(0, 0, 0);
}
.expirtes h3 ,h4{
    font-size: 20px;
    font-weight: 500;
}
.resume button {
    background-color: rgba(16, 116, 163, 0.77);
    font-size: 20px;
    font-weight: 600;
    outline: none;
    border-radius: 10px;
    border: none;
    transition: 0.3s;
    padding:15px 10px;
    cursor: pointer;
}
.resume button a {
    font-size: 18px;
    font-weight: 300;
    color: #000;
}
.resume button:hover {
    background-color:rgb(75 161 201 / 28%);
}
@media (max-width:710px){

    .name {
        font-size: 65px;
    }
}
@media (max-width:680px) {
        .expirtes p {
            font-size: 20px;
    }   
    .expirtes-info {
        padding: 3rem 0;
    }
    .name {
        font-size: 40px;
    }
}